import React,{useContext,useState} from 'react';
import {BiSearchAlt} from 'react-icons/bi';
import {FiBell} from 'react-icons/fi';
import {BsFillCaretDownFill} from 'react-icons/bs';
import avatar from '../assets/vivek.jpg';
import { DataContext } from './Dashboard';


export default function Navbar() {

  const context=useContext(DataContext);


  const [text,setText]=useState(null);

  //const [count,setCount]=useState(0)

  //const updatedContext=(key,data)=>{
  //  context.setapiData({...context.apidata,[key] :data})
  //}

  const handleSubmit=async(e)=>{
    e.preventDefault();
    let res1=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/twitter?ticker="+text)
    let formateres1=await  res1.json()


    let res2=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/technical?ticker="+text)
    let formateres2=await res2.json()

    let res3=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/newslist?ticker="+text)
    let formateres3=await res3.json()

    let res4=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/technical?ticker="+text)
    let formateres4=await res4.json()

    let res5=await fetch("https://stockanalysisapi-xyy5p4vcpa-ew.a.run.app/recommendation?ticker="+text)
    let formateres5=await res5.json()


    context.setapiData({...context.apidata,'advice_data':formateres5,'indicators_data':formateres4, 'news_data':formateres3,'lstm_data':formateres2,'twitter_data':formateres1})


  }

  return (
    <div className='navbar'>

        <div className= 'title'>
            <h2>DASHBOARD</h2>
            <span>Stocks Predictions Everyday</span>
        </div>



        <div className= 'container'>

        <form onSubmit={handleSubmit}  method="GET">



                <div className='search'>
                     <BiSearchAlt/>
                     <input type='text' onChange={e=>setText(e.target.value)} placeholder='Search ...' name="search_query"/>
                </div>

                <input className='button' type= 'submit' value='PREDICT'/>

</form>



                <div className= 'profile'>

                      <FiBell className='bell'/>

                      <div className='image'>
                      <img src={avatar} alt='' />
                      </div>

                      <div className='profile__name'>
                      <h4>Vivek Jain</h4>
                      <h6>Admin</h6>
                      </div>

                      <BsFillCaretDownFill/>


                </div>


      </div>



  </div>
  );
}
