import React, {useContext } from 'react';
import { PieChart, Pie, Cell} from 'recharts';
import logo from "../assets/twitter_logo.png";
import orange2 from '../assets/orange2.png';
import blue from '../assets/blue.png';
import {DataContext} from './Dashboard';


const COLORS = ['#0d88e8', '#F76E11'];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
      {`${(percent * 100).toFixed(0)}%`}
    </text>
  );
};


export default function Twitter() {

  const context=useContext(DataContext);

  const api_data= context.apidata.twitter_data;

  const filter_api_data= api_data.slice(-1);

  const data_final= [];

  for (let json of filter_api_data) {

    data_final.push({name: 'Group A', 'value': parseInt(json.pos)})
    data_final.push({name: 'Group B', 'value': parseInt(json.neg)})

}



    return (
      <div className='twitter'>
      <div className='header'><h1 className='title'>Twitter Sentiment:</h1></div>
      <div className='logo'><img src={logo} alt='logo'/></div>
        <PieChart width={250} height={260}  margin={{ top: 20, right: 0, bottom: 0, left: 0 }}>
          <Pie
            data={data_final}
            cx="50%"
            cy="50%"
            labelLine={false}
            label={renderCustomizedLabel}
            outerRadius={120}
            fill="#8884d8"
            dataKey="value"

          >
            {/*using data from context api value */}
            {data_final.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
        </PieChart>
        <div className='posneg'>
        <div className='pos'>
             <h1>Positive:</h1>
            <img src={blue} alt='' />
            </div>
            <div className='neg'>
              <h1>Negative:</h1>
             <img src={orange2} alt='' />
             </div>

        </div>
      </div>
    );

}
