import React, {useContext } from 'react';
import { DataContext } from './Dashboard';
import ReactApexChart from 'react-apexcharts';



export default function Graph() {

  const context=useContext(DataContext);

  ///////////////////////////////////////////

  const stock_data= context.apidata.news_data;

  const filter_stock_data= stock_data.slice(-1, stock_data.length);

  const stock= []

  for (let json of filter_stock_data) {

    stock.push(json.ticker)

}

///////////////////////////////////////////////////////

  const api_data= context.apidata.lstm_data;

  const filter_api_data= api_data.slice(-30, api_data.length); //last 30 values

  const data_final= [];

  for (let json of filter_api_data) {
    console.log(json)
    data_final.push({'x': new Date(json.Date), y:[json.Open,json.High,json.Low,json.Close] })

}


  //const prediction= context.apidata.news_data.close;
 let options= {
              chart: {
                type: 'candlestick',
                height: 250,
                id: 'candles',
                toolbar: {
                  autoSelected: 'pan',
                  show: true
                },
                zoom: {
                  enabled:true
                },
              },
              plotOptions: {
                candlestick: {
                  colors: {
                    upward: '#3C90EB',
                    downward: '#DF7D46'
                  }
                }
              },
              xaxis: {
                type: 'datetime'
              }
            }





  console.log(data_final)


  let series=[{
    name: 'Close Price',
    data: data_final
  },

{
    name: 'Predicted Price',
    data: data_final
  },


]


  return (


<div style={{'color':'black'}} className = 'lstm_model' >
<div className='header'>
<h1>LSTM MODEL</h1>
<div className='prediction'><h2>{stock[0]}  ➡️  Prediction for today is: 000 </h2></div>
</div>


    {/*using lstm_data value from context api value */}
    <div className='model'>
    <ReactApexChart key={Math.random()} options={options} series={series} type="candlestick" width={'100%'} height={'110%'} /></div>

  </div>
);
  }
