import React, {useContext } from 'react';
import {AiOutlineStock} from 'react-icons/ai';
import { DataContext } from './Dashboard';


export default function Milestones() {


  const context=useContext(DataContext);

  const api_data= context.apidata.news_data;

  const filter_api_data= api_data.slice(-1, api_data.length);

  const stock= []

  for (let json of filter_api_data) {

    stock.push(json.ticker)

}

///////////////////////////////////////////////////////
///API LSTM DATA
const api_close_data= context.apidata.lstm_data;

const close_data= api_close_data.slice(-30, api_close_data.length);
const closes= [];
const opens= [];

  for (let json of close_data) {

    closes.push(json.Close)
    opens.push(json.Open)

}


  const data= [

{

icon: AiOutlineStock,
title: stock[0]+' : Today Price:',
value: closes[29],
},

    {

      icon: AiOutlineStock,
      title: stock[0]+' : Yesterday Price:',
      value: opens[29],
    },

    {

      icon: AiOutlineStock,
      title: stock[0]+' : Last Week Price:',
      value: closes[23],
    },


    {

      icon: AiOutlineStock,
      title: stock[0]+' : Last Month Price:',
      value: closes[0],
    },


  ];


  return (

  <div className='milestones'>

    {
      data.map((milestone)=> {

      return (

        <div className='milestone'>
        <div className='icon'>
        <milestone.icon/>

        </div>

        <div className='details'>
        <h4>{milestone.title}</h4>
          <h2>{milestone.value}</h2>

        </div>

        </div>
      );

    })
    }
      </div>
  );
}
